<template>
<div class='wrap'>
    <div class="search">
        <el-input class="search-input" @input="filters" v-model="modelName" size="small" placeholder="搜索"></el-input>
    </div>
    <div class="list-product">
        <div class="title">我的组态</div>
        <ul class="list">
            <li v-for=" (item, index) of modelList" :key="index"> 
                <div>
                    <img src="@/assets/img/bg_1.jpg" alt="" srcset="">
                    <div class="name">{{item.name}}</div>
                    <div class="option">
                        <button class="primary">发布</button>
                        <button class="primary">编辑</button>
                        <button class="primary">预览</button>
                        <button class="danger">删除</button>
                    </div>
                </div>
            </li>
        </ul>
    </div>
</div>
</template>

<script>
export default {
    data () {
        return {
            modelName: '',
            allModelList: [{name: '组态_1'},
            {name: '组态_2'},{name: '组态_3'},
            {name: '组态_4'},{name: '组态_5'}],
            modelList: []
        }
    },
    methods: {
        filters () {
            if(this.modelName) {
                this.modelList = this.allModelList.filter( item => item.name.indexOf(this.modelName) != -1);
            }else{
                this.modelList = [...this.allModelList]
            }
        }
    },
    mounted () {
        this.modelList = [...this.allModelList]
    }
}
</script>

<style lang="scss" scoped>
    .wrap {
        height: 100vh;
        box-sizing: border-box;
        .search {
            display: flex;
            justify-content: flex-start;
            background: #545c64;
            align-items: center;
            border-bottom: 2px solid #409EFF;
            padding: 12px;
            .search-input {
                width: 200px;
                margin-right: 20px;
            }
        } 
        .list-product {
            padding-left: 20px;
            .title {
                font-size: 16px;
                font-weight: 600;
                color: #409EFF;
            }
            .list {
                list-style: none;
                padding: 0;
                margin: 0;
                display: flex;
                flex-wrap: wrap;
                height: calc(100vh - 125px);
                overflow: auto;
                li {
                    padding: 10px;
                    >div {
                        padding: 2px;
                        width: 200px;
                        border: 1px solid #ccc;
                        img {
                            width: 100%;
                            height: 200px;
                        }
                        .name {
                            font-size: 18px;
                            text-align: center;
                            border-bottom: 1px solid #ccc;
                            border-top: 1px solid #ccc;
                        }
                        .option {
                            padding-top: 5px;
                            display: flex;
                            flex-wrap: wrap;
                            justify-content: space-around;
                            .primary ,.danger {
                                border: none;
                                color: #eee;
                                padding: 5px;
                                cursor: pointer;
                            }
                            .primary {
                                background: #409EFF;
                            }
                            .danger {
                                background: orangered;
                            }
                        }
                    }
                }
            }
        }
    }
    
</style>